@import 'https://fonts.googleapis.com/css?family=Aldrich';
@import 'https://fonts.googleapis.com/css?family=Arvo';

$machine-color: linear-gradient(45deg,
    #111 5%,
    #222 40%,
    #222 60%,
    #111 95%,
  );

$button-color: linear-gradient(45deg,
    #111 0%,
    #222 45%,
    #222 55%,
    #111 100%,
  );

$button-color-off: linear-gradient(45deg,
    #111 0%,
    #111 45%,
    #111 55%,
    #111 100%,
  );

$button-color-focus: hsl(0, 75, 50);



body {
  margin: 0;
}

#root,
#app-container {
  align-items: center;
  background: hsl(40, 20, 15);
  display: flex;
  height: 100svh;
  justify-content: center;
  width: 100%;
}

#root * {
  box-sizing: border-box;
}

#drum-machine * {
  user-select: none;
}

#drum-machine {
  background: $machine-color;
  border: 5px outset #111;
  column-gap: 2rem;
  display: flex;
  flex-direction: row;
  height: 30rem;
  justify-content: space-between;
  padding: 2rem;
  width: 45rem;

  #machine-column-left {
    background: transparent;
    border-radius: 10px;
    border: 2px solid black;
    height: 100%;
    padding: 1em;
    width: 50%;

    /* DISABLED RULES

    .machine-screen {
      background: #070A1A;
      border-radius: 10px;
      border: 1px solid black;
      box-shadow: 0px 0px 10px #190D29;
      height: 100%;
      width: 50%;
    }
    
    */



    #machine-power-btn {
      background: none;
      border-radius: 50%;
      border: 1px solid black;
      height: 3em;
      padding: .5em;
      width: 3em;



      i {
        font-size: 2em;
      }

    }

    #machine-power-btn:focus {
      outline: none;
    }

    #machine-power-btn.power-on {
      border-color: $button-color-focus;



      i {
        color: $button-color-focus;
        outline: none;
        text-shadow: 0px 0px 20px $button-color-focus;
      }

    }

    #machine-power-btn.power-off {
      border-color: black;
      box-shadow: none;



      i {
        color: black;
        text-shadow: none;
      }

    }



    #machine-settings {
      label[for="volume-setting"] {
        color: #777;
        display: block;
        font-family: "arvo";
        font-size: 2rem;
        margin-top: 2rem;
        text-align: center;
      }

      #volume-setting {
        appearance: none;
        background-color: black;
        border: none;
        height: 10px;
        width: 100%;
      }

      #volume-setting:focus {
        outline: none;
      }

      #volume-setting::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        background-color: $button-color-focus;
        border: 1px solid black;
        cursor: pointer;
        height: 30px;
        width: 10px;
      }

      #volume-setting::-moz-range-thumb {
        background-color: $button-color-focus;
        border: 1px solid black;
        cursor: pointer;
        height: 30px;
        width: 10px;
      }

      #volume-setting::-ms-thumb {
        background-color: $button-color-focus;
        border: 1px solid black;
        cursor: pointer;
        height: 30px;
        width: 10px;
      }

    }

  }

  #machine-column-right {
    display: flex;
    flex-direction: column;
    height: 100%;
    row-gap: 2em;
    width: 50%;



    #drum-pad-btns {
      border-radius: 30px;
      border: 2px solid black;
      display: grid;
      gap: 1em;
      grid-template-columns: repeat(3, 1fr);
      height: calc(90% - 2rem);
      padding: 1em;
      width: 100%;

      button {
        border-radius: 10px;
        font-size: 2em;
        height: 5.5rem;
      }

      button:focus {
        outline: none;
      }

      button:active {
        border-style: outset;
      }

      .btn-off {
        background: $button-color-off;
        border: 1px outset #222;
        box-shadow: none;
        color: #222;
        text-shadow: none;
      }

      .btn-on-idle {
        background: $button-color;
        border: 1px outset #AAA;
        box-shadow: 0px 0px 5px #AAA;
        color: #AAA;
        text-shadow: 0px 0px 5px #AAA;
      }

      .btn-on-focus {
        background: $button-color;
        border-color: $button-color-focus;
        box-shadow: 0px 0px 10px $button-color-focus;
        color: $button-color-focus;
        text-shadow: 0px 0px 10px $button-color-focus;
      }

    }

    #display {
      background: black;
      border-radius: 5px;
      border: 1px solid #333;
      box-shadow: 0px 0px 10px #222;
      color: white;
      font-family: 'Aldrich';
      font-size: 2rem;
      margin-left: .5em;
      max-height: 3rem;
      min-height: 3rem;
      padding: .5rem .5rem 2rem;
      text-shadow: 0px 0px 2px white;
      width: calc(100% - 2rem);
    }

  }

}